//Font color for both hero and description text respectively
$hero-bg-clr: #070707;
$hero-dsc-clr: #000000;

//Font text-color for both hero and description text respectively
$hero-bg-ft: 6rem;
$hero-dsc-ft: 0.75rem;

$md-dsk: 1200px;

.h {
  height: 100vh;
  overflow: hidden;
  position: relative;

  &__wrapper {
    max-width: 75rem;
    margin: auto;
    position: relative;

    .h-value {
      margin-top: 6rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 10px;
      &__main {
        font-size: 20px;
      }

      &__desc {
        font-size: 0.9rem;
        max-width: 400px;
        line-height: 1.5;
      }
    }

    .h-text {
      font-size: 5.5rem;
      text-align: center;
      margin-top: 3rem;
      font-weight: 500;
      .m-b {
        font-weight: 800;
      }
    }

    .h-bottom {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 3rem;
      .h-desc {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;

        text-align: left;
        .h-desc-main {
          font-size: 20px;
          text-align: left;
          width: 100%;
        }

        .h-desc-d {
          font-size: 0.9rem;
          max-width: 400px;
          line-height: 1.5;
          text-align: justify;
        }
      }

      .scroll {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        text-decoration: none;
      }

      .start {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;

        .start-project {
          border-radius: 50px;
          background-color: #070707;
          text-decoration: none;
          color: rgb(231, 231, 231);
          padding: 1rem 2rem;
          transition: 300ms ease;
          &:hover {
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}

.id {
  display: flex;
  align-items: center;
  position: absolute;
  top: 40%;
  left: -130px;
  column-gap: 0.8rem;
  transform: rotate(90deg);

  .line {
    width: calc(200px / 16px) + rem;
    background-color: rgb(27, 27, 27);
    height: 0.5px;
  }

  .title {
    font-size: 17px;
    color: rgb(0, 0, 0);
    letter-spacing: 5px;
    font-weight: 500;
  }
}

@media (max-width: 567px) {
  id {
    display: none;
    background: red;
  }
}

.animate {
  animation: scroll 2s ease-in infinite;
}

@keyframes scroll {
  50% {
    transform: translateY(20px);
  }

  100% {
    transform: translateY(0);
  }
}

