            text-align: justify;
          }
        }

        .scroll {
          flex: 1;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          text-decoration: none;
        }

        .start {
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;

          .start-project {
            border-radius: 50px;
            background-color: #070707;
            text-decoration: none;
            color: rgb(231, 231, 231);
            padding: 1rem 2rem;
            transition: 300ms ease;
            &:hover {
              margin-bottom: 20px;
            }
          }
        }
      }
    }
  }

  .id {
    display: none;
  }

  .animate {
    animation: scroll 2s ease-in infinite;
  }
}
